@font-face {
  font-family: "Cormorant";
  font-display: swap;
  src: url("../fonts/cormorantRegular.woff2") format("woff2"), url("../fonts/cormorantRegular.woff") format("woff");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/robotoLight.woff2") format("woff2"), url("../fonts/robotoLight.woff") format("woff");
  font-style: normal;
  font-weight: 300;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 300;
  font-size: 18px;
  line-height: 27px;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

svg {
  display: block;
}

button {
  cursor: pointer;
}

.woman {
  position: relative;
  display: flex;
  width: 100%;
  max-width: 864px;
  border: 20px solid rgba(168, 68, 68, 0.4);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.woman__content {
  width: 100%;
  max-width: 412px;
  padding: 35px 0 0 49px;
  background: rgba(237, 232, 214, 0.62);
}

.woman__title {
  max-width: 287px;
  margin-top: 0;
  margin-bottom: 85px;
  font-family: "Cormorant", "Times New Roman", serif;
  font-weight: 400;
  font-size: 46px;
  line-height: 46px;
}

.woman__descr {
  margin: 0;
}
.woman__descr:first-of-type {
  max-width: 335px;
}

.woman__descr + .woman__descr {
  max-width: 287px;
  margin-top: 55px;
}

.woman__img-cloud-box {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  animation-name: cloud;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-direction: alternate;
}

.woman__img-cloud {
  position: absolute;
  z-index: -1;
}

.woman__img-cloud--first {
  top: 39px;
  left: 29px;
}

.woman__img-cloud--second {
  top: 158px;
  left: 91px;
}

.woman__img-cloud--third {
  top: 88px;
  left: 366px;
}

.woman__img-cloud--fourth {
  top: 141px;
  left: 602px;
}

@keyframes cloud {
  0% {
    transform: translateX(-30px);
  }
  100% {
    transform: translateX(30px);
  }
}
@media (max-width: 900px) {
  .woman__img-cloud-box {
    animation: none;
  }

  .woman__img-cloud--fourth {
    display: none;
  }
}
@media (max-width: 649px) {
  .woman__img-cloud {
    display: none;
  }

  .woman__img {
    display: none;
  }

  .woman__content {
    max-width: initial;
    padding: 35px 10px;
    text-align: center;
  }

  .woman__title {
    max-width: initial;
  }

  .woman__descr:first-of-type {
    max-width: initial;
  }

  .woman__descr + .woman__descr {
    max-width: initial;
  }
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1230px;
  height: 100%;
  padding: 0 15px;
  margin: 0 auto;
}